* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', sans-serif;
}

.app-wrapper {
  min-height: 100vh;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
}

.navbar {
  background: #4285f4;
  color: white;
  padding: 15px 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.app-container {
  display: flex;
  flex-direction: row;
  min-height: calc(100vh - 60px);
}

.left-sidebar {
  width: 300px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.95);
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

.task-creator {
  margin-bottom: 20px;
}

.task-creator input {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 20px;
  background: #f0f2f5;
  margin-bottom: 10px;
}

.button-group {
  display: flex;
  gap: 10px;
}

.task-creator button {
  flex: 1;
  padding: 10px 15px;
  border: none;
  background: #4285f4;
  color: white;
  border-radius: 20px;
  cursor: pointer;
  transition: transform 0.2s;
}

.task-creator button:hover {
  transform: scale(1.05);
}

.delete-btn {
  background: #ea4335;
}

.task-item {
  padding: 15px;
  margin: 10px 0;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  cursor: move;
  transition: all 0.3s ease;
}

.task-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

.main-content {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.daily-tasks {
  flex: 2;
  padding: 20px;
  background: white;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.task-card {
  padding: 15px;
  margin: 10px 0;
  background: #f8f9fa;
  border-radius: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: all 0.3s ease;
}

.task-card:hover {
  background: #e9ecef;
}

.task-card.completed {
  background: #d4edda;
  text-decoration: line-through;
  color: #155724;
}

.task-actions {
  display: flex;
  gap: 10px;
}

.complete-btn, .remove-btn {
  width: 24px;
  height: 24px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.2s;
}

.complete-btn {
  background: #34c759;
  color: white;
}

.remove-btn {
  background: #ea4335;
  color: white;
}

.complete-btn:hover, .remove-btn:hover {
  transform: scale(1.1);
}

.task-stats {
  display: flex;
  gap: 20px;
}

.stat-card {
  flex: 1;
  padding: 20px;
  background: white;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.stat-card h3 {
  color: #666;
  margin-bottom: 10px;
}

.stat-card p {
  font-size: 24px;
  font-weight: bold;
  color: #4285f4;
}

.task-visualization {
  background: white;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  margin: 0 auto;
}

/* Responsive Design */
@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }

  .left-sidebar {
    width: 100%;
    box-shadow: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

  .button-group {
    flex-direction: column;
  }

  .task-creator button {
    width: 100%;
  }

  .main-content {
    padding: 10px;
  }

  .task-stats {
    flex-direction: column;
  }

  .daily-tasks {
    margin-bottom: 20px;
  }

  .task-visualization {
    max-width: 100%;
  }
}

@media (max-width: 480px) {
  .navbar h1 {
    font-size: 1.2rem;
  }

  .task-card {
    padding: 10px;
    font-size: 0.9rem;
  }

  .stat-card p {
    font-size: 1.2rem;
  }
}
